import Vue from 'vue';
import App from './app.vue';
import VueRouter from 'vue-router';
import routes from './router/index.js';
import Qs from 'qs'
import {weChatAuth} from './assets/js/util'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import cookie from 'vux/src/tools/cookie/index.js'


const FastClick = require('fastclick')
FastClick.attach(document.body)

Vue.use(VueRouter)
Vue.use(AlertPlugin)
Vue.use(AjaxPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin, {position: 'middle'})
Vue.use(ConfirmPlugin)
Vue.use(DatetimePlugin)
Vue.use(WechatPlugin)







const router = new VueRouter(routes);


router.beforeEach((to, from, next) => {
let token = cookie.get('token') || '';

if (to.meta.noNeedLogin || to.path == '/login') {
next();
} else {
if (token) {

next();
} else {

next('/login')
}
}
});



Vue.http.interceptors.request.use(function (config) {
let userToken = cookie.get('token') || '';
config.timeout = 2000;
config.transformRequest = [function (data = {}, headers) {

data.token = userToken;
data.js_code = new Date().getTime();
data = Qs.stringify(data);
return data;
}];

return config;
}, function (error) {

return Promise.reject(error);
});


Vue.http.interceptors.response.use(function (response) {


let data = response.data;
if (data.code == 0) {
Vue.$vux.toast.text(data.msg, 'middle')
return Promise.reject(data.msg);
} else if (data.code == 401) {
Vue.$vux.toast.text('未登录', 'middle')

return Promise.reject(data.msg);
} else if (data.code == 403) {
Vue.$vux.toast.text('未授权', 'middle')


return Promise.reject(data.msg)

} else {
return data;
}
}, function (error) {

let status = error.response.status;
if (status == 0) {
Vue.$vux.toast.text(data.msg, 'middle')
} else if (status == 401) {
Vue.$vux.toast.text('未登录', 'middle')

} else if (status == 403) {
Vue.$vux.toast.text('未授权', 'middle')


} else {
Vue.$vux.toast.text('请求接口报错', 'middle')
}
return Promise.reject(error);
});

const app = new Vue({
el: '#app',
router,

template: '<App/>',
components: {App}
});



